  try {
    console.log('üöÄ [DEBUG] Phase 3 component mounted, starting initialization...')
    
    const rfpData = localStorage.getItem('selectedRFP')
    if (rfpData) {
      try {
        selectedRFP.value = JSON.parse(rfpData)
